// app/blog/[slug]/page.tsx or page.js
// 'use client';

import React,{} from 'react';

export default async function Page({ params }) {
  
  const { slug } = params;
  const url = `http://localhost:1337/api/articles?sort[0]=title:asc&filters${slug}[$eq]=javascript-tutorial&status=published&locale[0]=en&populate=*`;
  const response = await fetch(`${url}`,{cache: 'no-store'});
  const result = await response.json();
  console.log("Result from the slug",result);

  // You could fetch real blog post data here using the slug

  return (
    <div className="min-h-screen bg-gray-50 text-gray-800 px-4 py-10">
      <div className="max-w-3xl mx-auto">
        {/* Header */}
        <header className="mb-10">
          <h1 className="text-4xl font-bold tracking-tight mb-2 capitalize">
            {result.data[0].title}
          </h1>
          <p className="text-sm text-gray-500">Published on August 2, 2025</p>
        </header>

        {/* Content */}
        <div className="">
          {
            result.data[0] && result.data[0].blocks.filter({"__component": "shared.rich-text"}).map(item => {
              return <MarkdownHTML markdown={item.body} />
            })
          }
        </div>

        {/* Footer */}
        <footer className="mt-16 border-t pt-6 text-center text-sm text-gray-500">
          Â© {new Date().getFullYear()} My Blog. All rights reserved.
        </footer>
      </div>
    </div>
  );
}
